@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html
{
  -webkit-tap-highlight-color:transparent;
}

.app-text {
  background-color: #DEFCEA;
  font-family: 'Titillium Web', sans-serif;
  text-align: center;
  padding: 3% 0;

}

.app-text a {
  text-decoration: none;
  color: black;
}

.app-text a:hover {
  color: #5751BF;
}
/*Carousel */


.scene {
  position: relative;
  width: 210px;
  height: 140px;
  perspective: 1000px;
  margin: 10% auto;
  margin-bottom: 250px;

}

.carousel {
  width: 100%;
  height: 100%;
  position: absolute;
  transform: translateZ(-288px);
  transform-style: preserve-3d;
  transition: transform 1s;
}

.carousel__cell {
  position: absolute;
  width: 190px;
  height: 280px;
  left: 10px;
  top: 10px;
  line-height: 116px;
  font-size: 80px;
  font-weight: bold;
  text-align: center;
  
}


.carousel__cell:nth-child(9n+6) {  background-image: url("https://wahooart.com/Art.nsf/O/8EWH7D/$File/Georges-Pierre-Seurat-Modelo-de-Espaldas.JPG"); background-size:100% 100%; }
.carousel__cell:nth-child(9n+2) {  background-image: url("https://paris-luttes.info/IMG/jpg/p-re-peinard-2-4099669.jpg"); background-size:100% 100%; }
.carousel__cell:nth-child(9n+3) {  background-image: url("https://alchetron.com/cdn/mile-pouget-49ce55f5-61c6-4701-ad22-143a1d4a16f-resize-750.jpeg"); background-size:100% 100%; }
.carousel__cell:nth-child(9n+0) {  background-image: url("https://i.pinimg.com/originals/b8/78/fe/b878feb01189d9d66ad953205b771425.jpg"); background-size:100% 100%; }
.carousel__cell:nth-child(9n+1) {  background-image: url("https://collection.imamuseum.org/artwork/36845/0_full.jpg"); background-size:100% 100%; }
.carousel__cell:nth-child(9n+5) {  background-image: url("https://www.moma.org/media/W1siZiIsIjQ2OTA3MCJdLFsicCIsImNvbnZlcnQiLCItcXVhbGl0eSA5MCAtcmVzaXplIDIwMDB4MTQ0MFx1MDAzZSJdXQ.jpg?sha=7ef54b68629adfe8"); background-size:100% 100%; }
.carousel__cell:nth-child(9n+7) {  background-image: url("https://images-na.ssl-images-amazon.com/images/I/61cRqP1Zr5L._AC_SY741_.jpg"); background-size:100% 100%; }
.carousel__cell:nth-child(9n+8) {  background-image: url("https://images.metmuseum.org/CRDImages/dp/original/DT11780.jpg"); background-size:100% 100%; }
.carousel__cell:nth-child(9n+4) {  background-image: url("https://3h7pwd17k2h42n17eg2j7vdq-wpengine.netdna-ssl.com/wp-content/uploads/2020/11/58-SHEA-Signac_Opus-217-resized-2000x1597-1-820x550.jpg"); background-size:100% 100%; }

.carousel__cell:nth-child(1) { transform: rotateY(  0deg) translateZ(288px); }
.carousel__cell:nth-child(2) { transform: rotateY( 40deg) translateZ(288px); }
.carousel__cell:nth-child(3) { transform: rotateY( 80deg) translateZ(288px); }
.carousel__cell:nth-child(4) { transform: rotateY(120deg) translateZ(288px); }
.carousel__cell:nth-child(5) { transform: rotateY(160deg) translateZ(288px); }
.carousel__cell:nth-child(6) { transform: rotateY(200deg) translateZ(288px); }
.carousel__cell:nth-child(7) { transform: rotateY(240deg) translateZ(288px); }
.carousel__cell:nth-child(8) { transform: rotateY(280deg) translateZ(288px); }
.carousel__cell:nth-child(9) { transform: rotateY(320deg) translateZ(288px); }

.btn-div {
  display: block;
  text-align: center;
}
.btn-div button {
  background-color: transparent;
  border: none;
  width: 40%;
  font-size: 2rem;
}

.btn-div button:focus {outline:0;}
.btn-div button:hover {
  cursor: pointer;
}

